.home__container {
  height: 120vh;

  .bg__wrapper{
    width: 100%;
    height: 100%;
    position: relative;
    background: url(../../../assets/bg-home.svg) no-repeat center/cover;

    .content__wrapper {
      height: 100%;
      padding: 0 10%;
      // border: 1px solid green;

      .home__content {
        display: flex;
        flex-direction: column;
        top: 25%;
        position: relative;
        width: 475px;
        // color: white;
        // border: 1px solid red;

        h2 {
          font-size: 40px;
          font-weight: 600;
          line-height: 40px;
          color: #606d8e;
          margin-bottom: 16px;
        }

        h1 {
          font-size: 60px;
          font-weight: 700;
          line-height: 72px;
          color: #313849;
          margin-bottom: 48px;
        }

        p {
          font-size: 23px;
          font-weight: 400;
          line-height: 26px;
          color: #606d8e;
          margin-bottom: 60px;
        }
      }

      .home__content__mobile {
        display: none;
        position: relative;
        top: 120px;
        left: -10px;
        height: 500px;
        width: 316px;
        // border: 1px solid red;

        h2 {
          font-size: 30px;
          font-weight: 600;
          line-height: 34px;
          margin-bottom: 27px;
          color: #606d8e;
        }

        h3, h1 {
          font-size: 36px;
          font-weight: 700;
          line-height: 43px;
          color: #313849;
          // margin-bottom: 48px;
        }

        p {
          position: relative;
          bottom: 20px;
          font-size: 20px;
          font-weight: 400;
          line-height: 22px;
          color: #fff;
        }
      }
    }
  }
}

@media screen and (max-width: 768px)
{
  .home__container {
    height: 812px;
    .bg__wrapper {
      width: 100%;
      height: 100%;
      position: relative;
      background: url(../../../assets/responsive/bg-home.svg) no-repeat center/cover;
      .content__wrapper {
        height: 100%;
        .home__content {
          display: none;
        }
        .home__content__mobile {
          display: flex;
          flex-direction: column;
          h1 {
            margin-bottom: 30px;
          }
          div {
            margin-bottom: 300px;
          }
          p {
            width: 150px;
          }
        }
      }
    }
  }
}
